h6{
    position: relative;
    font-size: 18px;
    font-family: 'PoppinsBold';
    color: var(--ultradarkviolet);
    margin: 0px 0px 15px 0px; 
    padding: 0;
    top: -25px;
}

p{
    position: relative;
    font-size: 13px;
    font-family: 'PoppinsRegular';
    color: var(--grayisviolet);
    margin: 0;
    top: -20px;
}

.wrapper-card{
    width: 245px;
    height: 180px;
    background-color: white;
    padding: 30px;
    border-radius: 10px;
}

.Icon{
    position: relative;
    width: 40px;
    height: 40px;   
    padding:  20px;
    background-color: var(--darkviolet);
    border-radius: 40px;
    top:-70px;
    margin: 0;
}

.Icon img{
    width: 40px;
    height: 40px; 
    margin: 0;
}

