
.contenedorshorten {
 background-image: url(../img/bg-shorten-desktop.svg);
 background-repeat: no-repeat;
 background-color:var(--darkviolet);
 width: 900px;
 padding: 45px 55px;
 border-radius: 8px;
}

.contenedorshorten input{
    width: 660px;
    height: 30px;
    padding:  10px 0px 10px 30px;
    border-radius: 10px;
    border: 0;
}

.contenedorshorten button{
    width: 180px;
    height: 55px;
    margin-left: 20px;
    background-color: var(--cyan);
    border-radius: 10px;
    font-family: 'PoppinsBold';
    color: white;
    font-size: 18px;
    border: 0;
}

.infoShorten{
    width: 985px;
    height: 60px;
    padding: 0px 0px 0px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
    border-radius: 10px;
    margin: 20px 0px ;
}

.infoShorten p{
    font-family: 'PoppinsRegular';
    margin-top: 20px;
}

.infoShorten textarea{
    border: 0px;
    font-family: 'PoppinsRegular';
    color: var(--cyan);
    overflow: none;
    resize: none;
}

.infoShorten button{
    width: 90px;
    height: 35px;
    position: relative;
    background-color: var(--cyan);
    border-radius: 5px;
    font-family: 'PoppinsBold';
    color: white;
    font-size: 14px;
    border: 0px;
    top: -5px;
}

.infoShorten button:hover{
    background-color: var(--cyanclaro);
    color: white;
} 

.buttonchoice{
    width: 90px;
    height: 35px;
    margin-left: 20px;
    background-color: var(--cyan);
    border-radius: 5px;
    font-family: 'PoppinsBold';
    color: white;
    font-size: 14px;
    border: 10px;
}

.der{
    margin: 18px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10px;
}

.linkcorto{
    grid-area: 1/1/1/2;
}

.btncopy{
    grid-area: 1/2/1/3;
}

