:root{
  --cyan:#2ACFCF;
  --cyanclaro:#81dbdb;
  --darkviolet:#3A3054;
  --red:#F46161;
  --gray:#BFBFBF;
  --grayisviolet:#9D9AA7;
  --darkblue:#35313E;
  --ultradarkviolet:#222026;
  --lila:#f0f1f6;
 }
 
 svg{
  width: 32px; 
  height: 8px; 
  background-color: var(--cyan);
  margin-top: 80px;
}

.information{
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.information-texto{
  grid-area: 1/1/2/2;
}

.information h1{
  font-family: 'PoppinsBold';
  font-size: 70px;
  line-height: 90px;
  color: var(--darkblue);
  margin: 80px 0 0 0 ;

}

.information p{
  position: relative;
  width: 480px;
  font-family: 'PoppinsRegular';
  font-size: 20px;
  line-height: 35px;
  margin-top: 20px;
}

.information a{
  padding: 10px 35px;
  margin-top: 10px;
  background-color: var(--cyan);
  font-family: 'PoppinsBold';
  text-decoration: none;
  color: white;
  font-size: 15px;
  border-radius: 20px;
  border: 0px;
}

.information a:hover{
  background-color: var(--cyanclaro);
}

.information img{
  grid-area: 1/2/2/3;
}

.Shortlink{
  position: relative;
  margin: 100px 0 0 0;
  padding: 0px 50px;
  top: -70px;
}

.grey{
  background-color: var(--lila);
}

.Characteristics{
  height: 400px;
  margin: 40px 0 0 80px;
  padding: 0;
  display: grid;
  grid-template-columns: 0.7fr 0.18fr 0.7fr 0.18fr 0.8fr;
  grid-template-rows: repeat(3, 7%);
  column-gap: 1px;
}

#card-1{
  grid-area: 1/1/1/2;
}
.rect-1{
  grid-area: 4/2/4/3;
}
#card-2{
  grid-area: 2/3/2/4;
}
.rect-2{
  grid-area: 4/4/4/5;
}
#card-3{
  grid-area: 3/5/3/6;
}

.texto{
  margin: 80px 0;
}

.texto h3{
  font-family: 'PoppinsBold';
  font-size: 40px;
  text-align: center;
  color: var(--darkblue);
  margin: 0;
}

.texto p{
  font-family: 'PoppinsRegular';
  text-align: center;
  font-size: 18px;
  margin-top: 30px;
}

#footer{
  margin: 0;
}