
.footer{
    display: grid;
    grid-template-rows: repeat(2 1fr);
}

.morado{
    background-image: url(../img/bg-boost-desktop.svg);
    background-size: 1120px;
    background-repeat: no-repeat;
    background-color:var(--darkviolet);
    margin:0;
    width: auto;
    height: 150px;
    padding: 21px 0px;
}

.morado h2{
   font-family: 'PoppinsBold';
   font-size: 32px;
   text-align: center;
   color: white;
}

.morado a{
    padding: 10px 35px;
    margin-top: 10px;
    margin-left: 480px;
    background-color: var(--cyan);
    font-family: 'PoppinsBold';
    text-decoration: none;
    color: white;
    font-size: 15px;
    border-radius: 20px;
    border: 0px;
  }
  
  .morado a:hover{
    background-color: var(--cyanclaro);
  }
  

.negro{
    background-color: var(--ultradarkviolet);
    width: auto;
    margin: 0;
    padding: 30px 0;
    display: grid;
    grid-template-columns: repeat(5 auto);
    grid-template-rows: repeat(2 auto);
}

.negro h6{
    font-family: 'Poppinsregular';
    color: white;
    font-size: 12px;
    margin-top: 65px;
}

.negro ul,li {
   display: block;
   margin: 4px 0;
   padding: 0;
}

.negro ul{
    position: relative;
    top: -20px;
}

.negro a{
    font-family: 'Poppinsregular';
    color: var(--grayisviolet);
    text-decoration: none;
    font-size: 12px;
}

.negro a:hover{
    color: var(--cyan);
}

#logo{
    width: 90px;
    height: 25px;
    margin: 30px 120px;
    grid-area: 1/1/3/1;
}

.Features{
    margin: 0;
    grid-area: 1/2/3/2;
}

.Resourcees{
    margin: 0;
    grid-area: 1/3/3/3;
}

.Company{
    margin: 0;
    grid-area: 1/4/3/4;
}

.Socialmedia{
    position: relative;
    width: 180px;
    height: 10px;
    grid-area: 1/5/2/5;
    display: flex;
    top: -45px;
}

.Socialmedia a{
    width: 95px;
    height: 40px;
    padding: 0;
}

#icon svg{
    padding: 3px;
    width: 25px;
    height: 25px;
    background-color: transparent;
}

#icon path:hover{
    fill: var(--cyan);
}
  

